body {
  font-family: sans-serif;
  background-color: #eceff1;
}
.show-data {
  width: 100%;
  background-color: #cfd8dc;
  margin: 20px;
  padding: 10px;
  border-radius: 6px;
  box-shadow: inset;

}
.show-info {
  width: 100%;
}
.show-info h1 {
  margin: auto;
}
.show-data img{
  display: block;
}
@media (min-width: 800px) {
    .show-data {
      display: flex;
    }
    .show-info {
      padding: 10px;
    }
}